import React from 'react';
import SensorList from './SensorList';
import Container from '@material-ui/core/Container';
import { addLog } from './utils/applogs';
import Grid from '@material-ui/core/Grid';
import ValueButton from './ValueButton';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { Map } from 'react-lodash';

const groupBy = (array, prop) => {
  return array.reduce(function (groups, item) {
    const val = item[prop]
    groups[val] = groups[val] || []
    groups[val].push(item)
    return groups
  }, {})
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 440,
    marginTop: 10,
    marginBottom: 10
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    fontSize: 16,
    fontWeight: 700
  },
}));

const reloadData = (setSensorData, fromDateTime = new Date(new Date().getTime() - (6 * 3600 * 1000)), toDateTime = new Date()) => {
  addLog('reloadData', fromDateTime, toDateTime);
  //    fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensorValues?fromDateTime=${parseInt(fromDateTime.getTime()/1000,10)}&toDateTime=${parseInt(toDateTime.getTime()/1000,10)}`)
  fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensorValues?from=6`)
    .then(response => response.json())
    .then(json => {
      const items = groupBy(json.series, 'sensorid');
      console.log('reloadData', items);
      setSensorData({
        sensorData: {
          items,
          lastUpdated: new Date()
        }
      });
    })
    .catch((error) => {
      console.error('ERROR', error);
    });
}

export default function SensorsScreen() {
  const [state, setSensorData] = React.useState({
    sensorData: {
      items: [],
      lastUpdated: new Date()
    }
  });

  React.useEffect(() => {
    reloadData(setSensorData);
  }, []);

  const classes = useStyles();

  return (
    <Container maxWidth="sm">
      <div className={classes.root}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Grid container spacing={1}>
              <Map collection={["14520232", "1525834634", "1525834644", "1525834649"]} iteratee={sensorId =>
                <Grid item xs={6}>
                  <Paper className={classes.paper}><p>{state.sensorData.items[sensorId] && state.sensorData.items[sensorId][0].name}</p>
                    <div>
                      <Map collection={state.sensorData.items[sensorId]} iteratee={sensor =>
                        <ValueButton sensor={sensor} />
                      } />
                    </div>
                  </Paper>
                </Grid>
              } />
            </Grid>
          </Grid>
        </Grid>
      </div>
      {state.sensorData &&
        <SensorList reloadData={reloadData} sensorData={state.sensorData} />
      }
    </Container>
  );
}
